import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EmployeeComponent } from './employee.component';


const routes: Routes = [

    {
        path: '',
        component: EmployeeComponent,
        children: [
            {
                path: '',
                redirectTo: 'pegawai'
            },
            {
                path: 'pegawai',
                loadChildren: () => import('./pegawai/pegawai.module').then(mo => mo.PegawaiModule)
            },
            {
                path: 'permintaan-pegawai',
                loadChildren: () => import('./permintaan-pegawai/permintaan-pegawai.module').then(mo => mo.PermintaanPegawaiModule)
            },
            {
                path: 'pegawai-detail/:id_pegawai',
                loadChildren: () => import('./pegawi-detail/pegawi-detail.module').then(mo => mo.PegawiDetailModule)
            },

            {
                path: 'pelamar',
                loadChildren: () =>
                    import('./pelamar/pelamar.module').then(mod => mod.PelamarModule),
            },
            {
                path: 'detail-pelamar/:id_pelamar',
                loadChildren: () =>
                    import('./detail-pelamar/detail-pelamar.module').then(
                        mod => mod.DetailPelamarModule,
                    ),
            },
        ]

    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class EmployeeRoutingModule { }
